	module.exports = {"data":{"post":{"html":"<p>Here's a problem you won't see at Web 2.0 startups. Our bank charges us for buying coins. The price is 20 cents per roll, regardless of denomination. A roll of 25 2-Euro coins costs the same as a roll of 50 1-cent coins. We don't get credit for rolls of coin we bring in. Of course the coins we use most are the 1-cent coins. At 20 cents per 50-cent roll, that's a good profit margin... for the bank, not us. </p>\n<p>Strangely, we always have to buy 1-cent and 1-Euro coins, but we always have a surplus of 2-cent and 2-Euro coins.</p>","fields":{"title":"20 Cents Per Roll","date":"12 Oct 2007","url":"https://papascott.de/2007-10-12-20-cents-per-roll/"}}},"pathContext":{"slug":"/2007-10-12-20-cents-per-roll/","prev":{"fields":{"title":"Broken Window","date":"05 Oct 2007","slug":"/2007-10-05-broken-window/"}},"next":{"fields":{"title":"Help Wanted","date":"12 Oct 2007","slug":"/2007-10-12-help-wanted/"}}}}

